import { createRouter, createWebHistory } from "vue-router";
import Tables from "../views/Tables.vue";
import Billing from "../views/Billing.vue";
import VirtualReality from "../views/VirtualReality.vue";
import RTL from "../views/Rtl.vue";
import Profile from "../views/Profile.vue"; 
import Signup from "../views/Signup.vue";
import Signin from "../views/Signin.vue";
import Clients from "../views/Clients.vue"
import Commandes from "../views/Commande.vue"
import Sites from "../views/Sites.vue"
import Tickets from "../views/Tickets.vue"
import Plans from "../views/Plans.vue"
import ContactMedia from "../views/ContactMedia.vue"
import ContactCanada from "../views/contactCanada.vue";
import subscribers from "../views/subscribers.vue";
import subscribersCanada from "../views/subscribersCanada.vue";
import reviews from "../views/reviews.vue";
import main from "../views/main.vue";
const routes = [
  {
    path: "/",
    name: "/",
    redirect: "/main",
  },
  {
    path: "/main",
    name: "Main",
    component: main,
  },
  {
    path: "/clients",
    name: "Clients",
    component: Clients,
  },
  {
    path: "/commandes",
    name: "Commande",
    component: Commandes,
  },
  {
    path: "/sites",
    name: "Sites",
    component: Sites,
  },
  {
    path: "/tickets",
    name: "Tickets",
    component: Tickets,
  },
  {
    path: "/plans",
    name: "Plans",
    component: Plans,
  },
  {
    path: "/contactMedia",
    name: "Contact Winco Media",
    component: ContactMedia,
  },
  {
    path: "/contactCanada",
    name: "Contact Winco Canada",
    component: ContactCanada,
  },
  {
    path: "/subsMedia",
    name: "Subscribers Winco Media",
    component: subscribers,
  },
  {
    path: "/subsCanada",
    name: "Subscribers Winco Canada",
    component: subscribersCanada,
  },
  {
    path:"/reviews",
    name:"Reviews",
    component:reviews

  },
   {
    path: "/tables",
    name: "Tables",
    component: Tables,
  },
  {
    path: "/billing",
    name: "Billing",
    component: Billing,
  },
  {
    path: "/virtual-reality",
    name: "Virtual Reality",
    component: VirtualReality,
  },
  {
    path: "/rtl-page",
    name: "RTL",
    component: RTL,
  },
  {
    path: "/profile",
    name: "Profile",
    component: Profile,
  }, 
  {
    path: "/signin",
    name: "Signin",
    component: Signin,
  },
  {
    path: "/signup",
    name: "Signup",
    component: Signup,
  },
   
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  linkActiveClass: "active",
});

export default router;
