<script>
import axios from 'axios'
  import ArgonInput from "@/components/ArgonInput.vue";
export default {
  
  name: "clientsTable",
  components:{
    ArgonInput
  },
  data() {
    return {
      AllClients:[],
        nom:"",
        prenom:"",
        email:"",
        phone:"",
        id:0,
        token:document.cookie,
    }
  },

  methods:{

    checkToken(){
      axios.get('https://admin-api.wincomedia.com/api/checkToken/'+this.token).then((response)=>{
        console.log(response);
        
      })
      .catch((error)=>{
        console.log(error);
        this.$router.replace(this.$route.query.redirect || '/signin');

      })
    },
    
    getSingleClient(idClient){
      axios
    .get('https://admin-api.wincomedia.com/api/getSpecificClient/'+this.token+'/'+idClient)
    .then((response) => {
      
      this.nom=response.data.nomClient
      this.prenom=response.data.prenomClient
      this.email=response.data.email
      this.phone=response.data.phone
      this.id=idClient
      
    })

    .catch((error)=>{
      console.log(error);
    })
    },
  getClients() {
      axios
    .get('https://admin-api.wincomedia.com/api/ListClients/'+this.token)
    .then((response) => {
      
      this.AllClients = response.data
      
    })

    .catch((error)=>{
      console.log(error);
    })
  },

  async updateClient(){
    var bodyFormData = new FormData();
    bodyFormData.append('nomClient',this.nom);
    bodyFormData.append('prenomClient',this.prenom);
    bodyFormData.append('email',this.email);
    bodyFormData.append('phone',this.phone);
    bodyFormData.append('id',this.id);
    await axios.post("https://admin-api.wincomedia.com/api/updateClient/"+this.token,bodyFormData)
    .then((response)=>{
      console.log(response);
      this.nom='',
      this.prenom='',
      this.phone='',
      this.email='',
      this.id=0,
      this.getClients()

    })
    .catch((error)=>{
      console.log(error);
    })
  },

  deleteClient(idClient){
    axios
    .get('https://admin-api.wincomedia.com/api/removeClient/'+this.token+'/'+idClient)
    .then((response) => {
      console.log(response);
      this.getClients()
    })
    .catch((error)=>{
      console.log(error);
    })
  }
  },
  mounted() {
    this.checkToken();
  this.getClients();

}

  

  
};


</script>




<template>




      <!-- Modal -->
      <div class="modal fade" id="modifyClient" tabindex="-1" aria-labelledby="modifyClientLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="modifyClientLabel">Modify form</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">

        <div class="row">
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >First name</label
                  >
                  
                  <argon-input type="text" v-model="this.nom" />
                </div>
                <div class="col-md-8">
                  <label for="example-text-input" class="form-control-label"
                    >Last name</label
                  >
                  <argon-input type="text" value="" v-model="this.prenom" />
                </div>
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >Email adresse</label
                  >
                  <argon-input type="text" value="" v-model="this.email"/>
                </div>
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >Phone number</label
                  >
                  <argon-input type="text" value=""  v-model="this.phone"/>
                </div>


      </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary" data-bs-dismiss="modal" @click="updateClient()">Save changes</button>
      </div>
    </div>
  </div>
</div>


  <div class="row justify-content-center">
    <div class="col-lg-11 mb-lg-0 mb-4">

      <div class="card">
      <div class="card-header pb-0">
        <h6>Customers table</h6>
      </div>
      <div class="card-body px-0 pt-0 pb-2">
        <div class="table-responsive p-0">
          <table class="table align-items-center mb-0">
            <tbody>
              <tr v-for="client in AllClients" :key="client.id">
                <td>
                  <div class="d-flex px-2 py-1">
                    <div>
                        <i class="fa fa-user me-2" aria-hidden="true"></i>
                    </div>
                    <div class="d-flex flex-column justify-content-center">
                      <h6 class="mb-0 text-sm">{{ client.nomClient }} {{ client.prenomClient }}</h6>
                      <p class="text-xs text-secondary mb-0">{{ client.email }}</p>
                    </div>
                  </div>
                </td>

                <td>
                  <div class="d-flex px-2 py-1">
                    <div class="text-center">
                          <p class="mb-0 text-xs font-weight-bold">Phone:</p>
                          <h6 class="mb-0 text-sm">{{ client.phone }}</h6>
                        </div>
                  </div>
                </td>

                <td>
                  <div class="d-flex px-2 py-1">
                    <div class="text-center">
                          <p class="mb-0 text-xs font-weight-bold">Commandes:</p>
                          <h6 class="mb-0 text-sm">{{ client.nbrCommande }}</h6>
                        </div>
                  </div>
                </td>
                <td>
                  <div class="d-flex px-2 py-1">
                    <div class="text-center">
                          <p class="mb-0 text-xs font-weight-bold">Websites:</p>
                          <h6 class="mb-0 text-sm">{{ client.nbrSite  }}</h6>
                        </div>
                    
                  </div>
                </td>

                <td>
                  <div class="d-flex px-2 py-1">
                    <div class="text-center">
                          <p class="mb-0 text-xs font-weight-bold">Tickets:</p>
                          <h6 class="mb-0 text-sm">{{ client.nbrTicket  }}</h6>
                        </div>
                    
                  </div>
                </td>
                <td>
                  <div class="d-flex px-2 py-1">
                    <div class="text-center">
                          <p class="mb-0 text-xs font-weight-bold">Joined:</p>
                          <h6 class="mb-0 text-sm">{{ client.createAt}}</h6>
                        </div>
                    
                  </div>
                </td>

                <td>
                  <div class="d-flex px-2 py-1">
                    <div class="text-center">
                      <span class="badge badge-sm bg-gradient-info mx-1 cursor-pointer" data-bs-toggle="modal" data-bs-target="#modifyClient" @click="getSingleClient(client.id)">Edit</span>
                      <span class="badge badge-sm bg-gradient-danger mx-1 cursor-pointer" @click="deleteClient(client.id)">Delete</span>
                        </div>
                    
                  </div>
                </td>

                
                
              </tr>
              
            </tbody>
          </table>
        </div>
      </div>
    </div>



    </div>
  </div>
   
  </template>
  
