<script>
import axios from 'axios';
import ArgonInput from "@/components/ArgonInput.vue";
export default {
  
  name: "TicketClientTable",
  components:{
    ArgonInput
  },
  data() {
    return {
      AllTicketsClients:[],
      tciketId:0,
      ticketBody:"",
      replyContenu:"",
      ticketTitle:"",
      ticketReplay:[],
      ticketDate:Date,
      newReply:"",
      id_client:22,
      token:document.cookie
    }
  },

  methods:{
    checkToken(){
      axios.get('https://admin-api.wincomedia.com/api/checkToken/'+this.token).then((response)=>{
        console.log(response);
      })
      .catch((error)=>{
        console.log(error);
        this.$router.replace(this.$route.query.redirect || '/signin');
      })
    },
  getClients() {
      axios
    .get('https://admin-api.wincomedia.com/api/TicketList/'+this.token)
    .then((response) => {
      
      this.AllTicketsClients = response.data
      
    })
    .catch((error)=>{
      console.log(error);
    })
  },
  ticketDetails(id){
    axios.get('https://admin-api.wincomedia.com/api/ticketDetails/'+this.token+'/'+id).then((response)=>{
      console.log(response);
      this.tciketId=response.data.id;
      this.ticketBody=response.data.contenuTicket;
      this.ticketTitle=response.data.titre;
      this.ticketDate=response.data.created_at;
      this.ticketReplay=response.data.ticketReplays;
    }).catch((error)=>{
      console.log(error);
    })
  },
  insertReply(){
    var bodyFormData = new FormData();
    bodyFormData.append('id',this.tciketId);
    bodyFormData.append('contenu',this.replyContenu);
    bodyFormData.append('id_client',this.id_client);
    this.ticketDetails(this.tciketId);
    axios.post('https://admin-api.wincomedia.com/api/submitTicketReply/'+this.token,bodyFormData).then((response)=>{
      
      console.log(response);
    })
  },

ticketActivation(id,etat){
  axios.get("https://admin-api.wincomedia.com/api/activateTicket/"+this.token+'/'+id+"/"+etat)
  .then((response)=>{
    console.log(response);
    this.getClients();
  })
  .catch((error)=>{
    console.log(error);
  })
},
removeTicket(id){
  axios.get("https://admin-api.wincomedia.com/api/removeTicket/"+this.token+'/'+id).then((response)=>{
    this.getClients();
    console.log(response);
  })
  .catch((error)=>{
    console.log(error);
  })
}

  },
  mounted() {
    this.checkToken();

  this.getClients();
  console.log(this.AllTicketsClients);
}

  

  
};


</script>




<template>




<div class="modal fade" id="modificationModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">{{ this.ticketTitle }}</h5>
        <div>{{ this.ticketDate }}</div>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
        
       <p> {{ this.ticketBody }}</p>
        
        <div class="card shadow-lg mb-2" v-for="reply in this.ticketReplay" :key="reply.id">
        <div class="card-body"> 
          <h6 class="card-title">Reply</h6>
        <p class="card-text">{{ reply.contenu }}</p>
      </div> 
        </div>





        <div class="row">
                <div class="col">
                  <label for="example-text-input" class="form-control-label"
                    >Reply :</label
                  >
                  
                  <argon-input type="text" v-model="this.replyContenu" />
                </div>
                
                
              </div>



      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary"  data-bs-dismiss="modal" @click="insertReply()">Add reply</button>
      </div>
    </div>
  </div>
</div>



    <div class="card">
      <div class="card-header pb-0">
        <h6>Tickets table</h6>
      </div>
      <div class="card-body px-0 pt-0 pb-2">
        <div class="table-responsive p-0">
          <table class="table align-items-center mb-0">
            <thead>
              <tr>
                <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Sujet</th>
                <th
                  class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2"
                >Nom site</th>

                
                <th
                  class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
                >Domaine</th>
                <th
                  class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
                >Client</th>
                <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Urgence</th>
                <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Etat</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="ticket in AllTicketsClients" :key="ticket.id">
                <td>
                  <div class="d-flex px-2 py-1">
                    <div>
                        <i class="fa fa-ticket me-2" aria-hidden="true"></i>
                    </div>
                    <div class="d-flex flex-column justify-content-center">
                      <h6 class="mb-0 text-sm">{{ ticket.titre }} </h6>
                      <p class="text-xs text-secondary mb-0"></p>
                    </div>
                  </div>
                </td>
                <td>
                  <p class="text-xs font-weight-bold mb-0">{{ ticket.nom_site }}</p>
                  
                </td>
                <td class="align-middle text-center text-sm">
                    <p class="text-xs font-weight-bold mb-0">{{ ticket.domaine_site }}</p>
                </td>
                <td class="align-middle text-center">
                    <div class="d-flex px-2 py-1">
                    <div>
                        <i class="fa fa-user me-2" aria-hidden="true"></i>
                    </div>
                    <div class="d-flex flex-column justify-content-center">
                      <h6 class="mb-0 text-sm">{{ ticket.nomComplet}}</h6>
                      <p class="text-xs text-secondary mb-0">{{ ticket.email }}</p>
                    </div>
                  </div>
                </td>
                <td class="align-middle text-center text-sm">
                    <span class="badge badge-sm bg-gradient-success" v-if="ticket.niveauUrgence === 1">Faible</span>
                    <span class="badge badge-sm bg-gradient-warning" v-else-if="ticket.niveauUrgence === 2">Moyen</span>
                    <span class="badge badge-sm bg-gradient-danger" v-else-if="ticket.niveauUrgence === 3">Critique</span>
                </td>
                <td class="align-middle text-center text-sm">
                    <span class="badge badge-sm bg-gradient-info mx-1 cursor-pointer" v-if="ticket.Etat === 0" @click="ticketActivation(ticket.id,1)">Ouvert</span>
                    <span class="badge badge-sm bg-gradient-warning mx-1 cursor-pointer" v-else-if="ticket.Etat === 1" @click="ticketActivation(ticket.id,0)">Fermer</span>
                    <span class="badge badge-sm bg-gradient-success cursor-pointer" data-bs-toggle="modal" data-bs-target="#modificationModal" @click="ticketDetails(ticket.id)">Reply</span>
                    <span class="badge badge-sm bg-gradient-danger mx-1 cursor-pointer" @click="removeTicket(ticket.id)">Delete</span>
                </td>
              </tr>
              
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </template>
  
