<script>
import axios from 'axios';

export default{
    name:'contactTable',
    data(){
        return{
        AllMessages:[],
        messageContenu:"",
        token:document.cookie
        }
    },
    methods:{
      checkToken(){
      axios.get('https://admin-api.wincomedia.com/api/checkToken/'+this.token).then((response)=>{
        console.log(response);
      })
      .catch((error)=>{
        console.log(error);
        this.$router.replace(this.$route.query.redirect || '/signin');

      })
    },
        getAllMessages(){
            axios.get("https://admin-api.wincomedia.com/api/getallContactMedia/"+this.token).then((response)=>{
                console.log(response);
                this.AllMessages=response.data
            })
            .catch((error)=>{
                console.log(error);
            })
        },

        messageContent(id){
            this.messageContenu="";
            this.messageContenu=this.AllMessages.find((message)=> message.id==id).messageContenu;
            
        },
        markAsRead(id,activation){
            axios.get("https://admin-api.wincomedia.com/api/MarkRead/"+this.token+'/'+id+"/"+activation)
            .then((response)=>{
                console.log(response);
                this.getAllMessages();
            })
            .catch((error)=>{
                console.log(error);
            })
        }

    },
    mounted(){
      this.checkToken();
        this.getAllMessages();
    }
}

</script>
<template>




      <!-- Modal -->
      <div class="modal fade" id="modifyClient" tabindex="-1" aria-labelledby="modifyClientLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="modifyClientLabel">Contact us Messages</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">

        {{ this.messageContenu }}

      
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
      </div>
    </div>
  </div>
</div>





<div class="row justify-content-center">
    <div class="col-lg-8 mb-lg-0 mb-4">

      <div class="card">
      <div class="card-header pb-0">
        <h6>Contact table</h6>
      </div>
      <div class="card-body px-0 pt-0 pb-2">
        <div class="table-responsive p-0">
          <table class="table align-items-center mb-0">
            <tbody>
              <tr v-for="contact in this.AllMessages" :key="contact.id">
                <td>
                  <div class="d-flex px-2 py-1">
                    <div>
                        <i class="fa fa-user me-2" aria-hidden="true"></i>
                    </div>
                    <div class="d-flex flex-column justify-content-center">
                      <h6 class="mb-0 text-sm">{{ contact.nomComplet }}</h6>
                      <p class="text-xs text-secondary mb-0">{{ contact.email }}</p>
                    </div>
                  </div>
                </td>

                

                
                

                
                <td>
                  <div class="d-flex px-2 py-1">
                    <div class="text-center">
                          <p class="mb-0 text-xs font-weight-bold">Send Date:</p>
                          <h6 class="mb-0 text-sm">{{new Date( contact.createdAt.timestamp*1000).toLocaleDateString()}}</h6>
                        </div>
                    
                  </div>
                </td>

                <td>
                  <div class="d-flex px-2 py-1">
                    <div class="text-center">
                      <span class="badge badge-sm bg-gradient-info mx-1 cursor-pointer" data-bs-toggle="modal" data-bs-target="#modifyClient" @click="messageContent(contact.id)">Message Details</span>
                      <span class="badge badge-sm bg-gradient-success mx-1 cursor-pointer" v-if="contact.lu===0" @click="markAsRead(contact.id,1)">Mark as Read</span>
                      <span class="badge badge-sm bg-gradient-warning mx-1 cursor-pointer" v-else @click="markAsRead(contact.id,0)">Mark as unRead</span>
                        </div>
                    
                  </div>
                </td>

                
                
              </tr>
              
            </tbody>
          </table>
        </div>
      </div>
    </div>



    </div>
  </div>
</template>