<template>

    <div class="card p-5" style="height:300px;width:90%">
        <h1>Hello Sir Admin !</h1>
       
        
    </div>
</template>
<script>
import axios from 'axios';
export default {
    name:"main",
    data(){
        return{
            token:document.cookie
        }
    },
    methods:{
        checkToken(){
        
        axios.get('https://admin-api.wincomedia.com/api/checkToken/'+this.token).then((response)=>{
        console.log(response);
        
      })
      .catch((error)=>{
        console.log(error);
        this.$router.replace(this.$route.query.redirect || '/signin');

      })
    }
    },

    mounted(){
        this.checkToken();
    }

}
</script>