<script>
import axios from 'axios';

export default{
    name:'reviews',
    data(){
        return {
            AllReviews:[],
            showedReview:"",
            token:document.cookie

        }
    },
    methods:{
      checkToken(){
      axios.get('https://admin-api.wincomedia.com/api/checkToken/'+this.token).then((response)=>{
        console.log(response);
      })
      .catch((error)=>{
        console.log(error);
        this.$router.replace(this.$route.query.redirect || '/signin');
      })
    },
        getAllReviews(){
        axios.get('https://admin-api.wincomedia.com/api/getallReviews/'+this.token).then((response)=>{
            console.log(response);
            this.AllReviews=response.data;
        })
        .catch((error)=>{
            console.log(error);
        })
        },
        activateReview(id,state){
            axios.get('https://admin-api.wincomedia.com/api/activateReview/'+this.token+'/'+id+"/"+state).then((response)=>{
                console.log(response);
                this.getAllReviews();

            })
            .catch((error)=>{
                console.log(error);
            })

        },

        getSingleReview(id){
            this.showedReview=this.AllReviews.find((review)=>review.id==id).content
        }

    },

    

    mounted(){
        this.checkToken();
        this.getAllReviews();
        
    }
}
</script>
<template>



      <!-- Modal -->
      <div class="modal fade" id="modifyClient" tabindex="-1" aria-labelledby="modifyClientLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="modifyClientLabel">Review Details</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">

        {{ this.showedReview }}

      
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
      </div>
    </div>
  </div>
</div>




<div class="row justify-content-center">
    <div class="col-lg-10 mb-lg-0 mb-4">

      <div class="card">
      <div class="card-header pb-0">
        <h6>Reviews Table</h6>
      </div>
      <div class="card-body px-0 pt-0 pb-2">
        <div class="table-responsive p-0">
          <table class="table align-items-center mb-0">
            <tbody>
              <tr v-for="review in this.AllReviews" :key="review.id">
                <td>
                  <div class="d-flex px-2 py-1">
                    <div>
                        <i class="fa fa-star text-success" aria-hidden="true" v-for="i in review.rating" :key="i"></i>
                    </div>
                    
                  </div>
                </td>

                <td>
                  <div class="d-flex px-2 py-1">

                    <div class="d-flex flex-column justify-content-center">
                      <h6 class="mb-0 text-sm">{{ review.idClient.email }}</h6>
                      
                    </div>
                  </div>
                </td>

                <td>
                  <div class="d-flex px-2 py-1">

                    <div class="d-flex flex-column justify-content-center">
                      <p class="mb-0 text-sm">Submitted {{ new Date(review.createAt.timestamp*1000).toLocaleDateString() }}</p>
                      
                    </div>
                  </div>
                </td>
                    <td>
                    <span class="badge badge-sm bg-gradient-info mx-1 cursor-pointer" data-bs-toggle="modal" data-bs-target="#modifyClient" @click="getSingleReview(review.id)">Review detail</span>
                      <span class="badge badge-sm bg-gradient-success mx-1 cursor-pointer" v-if="review.isActivated===1" @click="activateReview(review.id,0)">Unshow home</span>
                      <span class="badge badge-sm bg-gradient-warning mx-1 cursor-pointer" v-else @click="activateReview(review.id,1)">Show home</span>

                    </td>
                
              </tr>
              
            </tbody>
          </table>
        </div>
      </div>
    </div>
</div>
  </div>
</template>