<template>
    <div class="py-4 container-fluid">
      
      <div class=" row">
        <div class="col-12">
          
          <plansTable v-if="planInsert"/>
          <plansTable v-else/>
        </div>
      </div>



      <div class="py-4 container-fluid">
      <div class="row">
        <div class="col">
          <div class="card">
            <div class="card-header pb-0">
              <div class="d-flex align-items-center">
                <h2>New Plan form</h2>
                
              </div>
            </div>
            <div class="card-body">
              <p class="text-uppercase text-sm">PLAN INFORMATION</p>
              <div class="alert alert-success" v-if="planInsert" role="alert">
                Plan inserted successfully !
              </div>
              <div class="row">
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >Plan name</label
                  >
                  
                  <argon-input type="text" v-model="this.nomPlan" />
                </div>
                <div class="col-md-8">
                  <label for="example-text-input" class="form-control-label"
                    >Plan description</label
                  >
                  <argon-input type="text" value="" v-model="this.descPlan" />
                </div>
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >How many pages ? </label
                  >
                  <argon-input type="text" value="" v-model="this.nbrPage"/>
                </div>
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >How many modifications ?</label
                  >
                  <argon-input type="text" value=""  v-model="this.nbrModification"/>
                </div>
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >How many posts per month ?</label
                  >
                  <argon-input  type="text" v-model="this.nbrPost" />
                </div>
                <div class="col-md-5">
                  <label for="example-text-input" class="form-control-label"
                    >Monthly price</label
                  >
                  <argon-input type="text" value="" v-model="this.prixMensuel"/>
                </div>
                <div class="col-md-5">
                  <label for="example-text-input" class="form-control-label"
                    >Deployment price</label
                  >
                  <argon-input type="text" value="" v-model="this.prixEnligne"/>
                </div>
              </div>
              <argon-button color="success" size="sm" class="ms-auto"
                  @click="insertPlan()">SAVE PLAN</argon-button
                >
              <hr class="horizontal dark" />
              
               
            </div>
          </div>
        </div>
        
      </div>
    </div>
      


    
    </div>
  </template>
  
  <script>
  import plansTable from "./components/PlansTable.vue";
import ArgonInput from "@/components/ArgonInput.vue";
import ArgonButton from "@/components/ArgonButton.vue";
import axios from 'axios';
  
  export default {
    name: "Plans",
    components: {
      plansTable,
      ArgonInput,
      ArgonButton
    },
    data() {
      return{
      nomPlan:'',
      descPlan:'',
      nbrPage:0,
      nbrPost:0,
      nbrModification:0,
      prixMensuel:0,
      prixEnligne:0,
      planInsert:false,
      token:document.cookie
      }
      
    },

    methods:{
    async insertPlan() {
      var bodyFormData = new FormData();
      
      bodyFormData.append('nom_plan',this.nomPlan)
      bodyFormData.append('desc_plan',this.descPlan)
      bodyFormData.append('nbr_page',this.nbrPage)
      bodyFormData.append('nbr_post',this.nbrPost)
      bodyFormData.append('nbr_modification',this.nbrModification)
      bodyFormData.append('prix_mensuel_plan',this.prixMensuel)
      bodyFormData.append('prix_mise_enligne_plan',this.prixEnligne)
     await axios.post("https://admin-api.wincomedia.com/api/submitNewPlan/"+this.token,bodyFormData)
    .then((response)=>{
      console.log(response);
      this.nomPlan=''
      this.descPlan=''
      this.nbrPage=0
      this.nbrPost=0
      this.nbrModification=0
      this.prixMensuel=0
      this.prixEnligne=0
      this.planInsert=true
      
    })
    .catch(error => {
      console.log(error);
    });
    
  },
  },

    mounted() {

  }

  };
  </script>
  