<script>
import axios from 'axios'
import ArgonInput from "@/components/ArgonInput.vue";
export default {
  
  name: "SiteClientTable",
  components:{
    ArgonInput,
  },
  data() {
    return {
      AllSitesClients:[],
      id:0,
      nom_site:"",
      domaine_site:"",
      type_site:"",
      description_site:"",
      images_site:"",
      id_plan:0,
      nom_plan:"",
      AllPlans:[],
      token:document.cookie
    }
  },

  methods:{
    checkToken(){
      axios.get('https://admin-api.wincomedia.com/api/checkToken/'+this.token).then((response)=>{
        console.log(response);

      })
      .catch((error)=>{
        console.log(error);
        this.$router.replace(this.$route.query.redirect || '/signin');
      })
    },
  getClients() {
      axios
    .get('https://admin-api.wincomedia.com/api/siteClientList/'+this.token)
    .then((response) => {
      
      this.AllSitesClients = response.data
      
    })
    .catch((error)=>{
      console.log(error);
    })
  },

selectedDdlValue(id,nom){
  this.id_plan=id;
  this.nom_plan=nom;
},
remplirDdlList(){

  axios.get("https://admin-api.wincomedia.com/api/getAllPlans").then((response)=>{
    this.AllPlans=response.data;

  })
  .catch((error)=>{
    console.log(error);
  })
},

  getSingleWebsite(idSite){
    axios
    .get('https://admin-api.wincomedia.com/api/singleSiteClient/'+this.token+'/'+idSite)
    .then((response) => {
      this.id=response.data.id;
      this.nom_site=response.data.nomSite;
      this.domaine_site=response.data.domaineSite;
      this.type_site=response.data.typeSite;
      this.description_site=response.data.nomSite;
      this.id_plan=response.data.idPlan.id;
      this.nom_plan=response.data.idPlan.nomPlan
      this.images_site=response.data.imagesUrl;
      this.remplirDdlList();
      
    })
    .catch((error)=>{
      console.log(error);
    })
  },
  updateWebsite(){
    var bodyFormData = new FormData();
    bodyFormData.append('id',this.id);
    bodyFormData.append('id_plan',this.id_plan);
    bodyFormData.append('nom_site',this.nom_site);
    bodyFormData.append('domaine_site',this.domaine_site);
    bodyFormData.append('type_site',this.type_site);
    bodyFormData.append('desc_site',this.description_site);
    bodyFormData.append('images_url',this.images_site);
    axios.post('https://admin-api.wincomedia.com/api/updateWebsite'+'/'+this.token,bodyFormData).then((response)=>{
      console.log(response);
      this.getClients();
    })
    .catch((error)=>{
      console.log(error);
    })
  },
  removeWebsite(id){
    axios.get('https://admin-api.wincomedia.com/api/removeWebsite/'+this.token+'/'+id).then((response)=>{
      this.getClients();
      console.log(response);
    })
    .catch((error)=>{
      console.log(error);
    })
  }
  },
  mounted() {
    this.checkToken();

  this.getClients();
  console.log(this.AllSitesClients);
}

  

  
};


</script>




<template>


<div class="modal fade" id="modificationModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">Website form modification</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
        

        <label for="example-text-input" class="form-control-label"
                    >Plan : {{ nom_plan }}</label
                  ><br>
        <div class="btn-group">
  <button type="button" class="btn btn-danger dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
    Action
  </button>

  <ul class="dropdown-menu">
    <li v-for="plan in this.AllPlans" :key="plan.id" @click="selectedDdlValue(plan.id,plan.nomPlan)">
    <a class="dropdown-item" href="#">{{ plan.nomPlan }}</a>
    </li>

  </ul>
</div>




        <div class="row">
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >Website name</label
                  >
                  
                  <argon-input type="text" v-model="this.nom_site" />
                </div>
                <div class="col-md-8">
                  <label for="example-text-input" class="form-control-label"
                    >Website domaine</label
                  >
                  <argon-input type="text" value="" v-model="this.domaine_site" />
                </div>
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >Website type </label
                  >
                  <argon-input type="text" value="" v-model="this.type_site"/>
                </div>
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >website Description</label
                  >
                  <argon-input type="text" value=""  v-model="this.description_site"/>
                </div>
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >Website image sample</label
                  >
                  <argon-input  type="text" v-model="this.images_site" />
                </div>
                
              </div>



      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary"  data-bs-dismiss="modal" @click="updateWebsite()">Save changes</button>
      </div>
    </div>
  </div>
</div>









    <div class="card">
      <div class="card-header pb-0">
        <h6>Website table</h6>
      </div>
      <div class="card-body px-0 pt-0 pb-2">
        <div class="table-responsive p-0">
          <table class="table align-items-center mb-0">
            <tbody>
              <tr v-for="site in AllSitesClients" :key="site.id">
                <td>
                  <div class="d-flex px-2 py-1">
                    <div>
                        <i class="fa fa-globe me-2" aria-hidden="true"></i>
                    </div>
                    <div class="d-flex flex-column justify-content-center">
                      <h6 class="mb-0 text-sm">{{ site.nom_site }} </h6>
                      <p class="text-xs text-secondary mb-0"></p>
                    </div>
                  </div>
                </td>
                <td>
                  <p class="text-xs font-weight-bold mb-0">{{ site.domaine_site }}</p>
                  
                </td>
                <td class="align-middle text-center text-sm">
                  <span class="badge badge-sm bg-gradient-success">{{ site.type_site }}</span>
                </td>
                <td class="align-middle text-center">
                    <div class="d-flex px-2 py-1">
                    <div>
                        <i class="fa fa-user me-2" aria-hidden="true"></i>
                    </div>
                    <div class="d-flex flex-column justify-content-center">
                      <h6 class="mb-0 text-sm">{{ site.nomComplet}}</h6>
                      <p class="text-xs text-secondary mb-0">{{ site.email }}</p>
                    </div>
                  </div>
                </td>
                <td class="align-middle text-center">
                    <div class="d-flex px-2 py-1">
                    
                    <div class="d-flex flex-column justify-content-center">
                      
                      <h6 class="mb-0 text-sm">{{ site.nom_plan}}</h6>
                      <p class="text-xs text-secondary mb-0">Plan </p>
                      
                    </div>
                  </div>
                </td>
                <td class="align-middle">
                  <span class="badge badge-sm bg-gradient-info mx-1 my-1 cursor-pointer" data-bs-toggle="modal" data-bs-target="#modificationModal" @click="getSingleWebsite(site.id)">Edit</span>
                  <span class="badge badge-sm bg-gradient-danger mx-1 my-1 cursor-pointer" @click="removeWebsite(site.id)">Delete</span>
                </td>
              </tr>
              
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </template>
  
