<script>
import axios from 'axios';
export default{
    name:"subscribers",
    data(){
        return{
            AllSubs:[],
            token:document.cookie

        }
    },

    methods:{
        checkToken(){
      axios.get('https://admin-api.wincomedia.com/api/checkToken/'+this.token).then((response)=>{
        console.log(response);
      })
      .catch((error)=>{
        console.log(error);
        this.$router.replace(this.$route.query.redirect || '/signin');
      })
    },

        getAllSubs(){
            axios.get('https://admin-api.wincomedia.com/api/getAllSubs/'+this.token).
            then((response)=>{
                console.log(response)
                this.AllSubs=response.data;
            })
            .catch((error)=>{
                console.log(error);
            })
        },

        activateSub(id,activation){

            axios.get('https://admin-api.wincomedia.com/api/activateSub/'+this.token+'/'+activation+'/'+id).then((response)=>{
                console.log(response);
                this.getAllSubs();
            })
            .catch((error)=>{
                console.log(error);
            })

        },

        deleteSub(id){
            axios.get('https://admin-api.wincomedia.com/api/removeSub/'+this.token+'/'+id).then((response)=>{
                console.log(response);
                this.getAllSubs();
            })
            .catch((error)=>{
                console.log(error);
            })
        }

    },

    mounted(){
        this.checkToken();

        this.getAllSubs();

    }
}
</script>
<template>

<div class="py-4 container-fluid">
      <div class=" row">
        <div class="col-3" v-for="sub in this.AllSubs" :key="sub.id">
          

            <div class="mb-4 card"><div class="p-3 card-body"><div class="d-flex justify-content-center">
                <div class=""><div class="numbers">
                    <h6 >{{sub.email}}</h6>
                    <span class="badge badge-sm bg-gradient-warning mx-1 cursor-pointer" v-if="sub.status===1" @click="activateSub(sub.id,0)">Block</span>
                    <span class="badge badge-sm bg-gradient-success mx-1 cursor-pointer" v-else @click="activateSub(sub.id,1)">unBlock</span>
                    <span class="badge badge-sm bg-gradient-danger mx-1 cursor-pointer" @click="deleteSub(sub.id)">Delete</span>
                    <span class="text-sm">{{ new Date(sub.createdAt.timestamp*1000).toLocaleDateString() }}</span>
                </div></div></div></div></div>



        </div>
      </div>
      
    </div>
</template>