<script>
import ArgonInput from "@/components/ArgonInput.vue";

import axios from 'axios'
export default {
  
  name: "plansTable",
  components:{
    ArgonInput,
      
  },
  data() {
    return {
      AllPlans:[],
      nomPlan:'',
      descPlan:'',
      nbrPage:0,
      nbrPost:0,
      nbrModification:0,
      prixMensuel:0,
      prixEnligne:0,
      planInsert:false,
      idPlan:0,
      specificPlan:[],
      token:document.cookie
    }
  },

  methods:{
    checkToken(){
      axios.get('https://admin-api.wincomedia.com/api/checkToken/'+this.token).then((response)=>{
        console.log(response);
       
      })
      .catch((error)=>{
        console.log(error);
        this.$router.replace(this.$route.query.redirect || '/signin');
      })
    },
  getPlans() {
      axios
    .get('https://admin-api.wincomedia.com/api/getAllPlans/'+this.token)
    .then((response) => {
      
      this.AllPlans = response.data;
      
    })
    .catch((error)=>{
      console.log(error);
    })
  },

  async getSpecificPlans(idPlan) {
     await axios
    .get('https://admin-api.wincomedia.com/api/getSpecificPlan/'+this.token+'/'+idPlan)
    .then((response) => {
      this.idPlan=response.data.id
      this.nomPlan=response.data.nomPlan
      this.descPlan=response.data.descPlan
      this.nbrModification=response.data.nbrModification
      this.nbrPage=response.data.nbrPage
      this.nbrPost=response.data.nbrPost
      this.prixMensuel=response.data.prixMensuelPlan
      this.prixEnligne=response.data.prixMiseEnLignePlan
      
    })
  },

  activatePlan(idPlan,activation){
    axios
    .get('https://admin-api.wincomedia.com/api/activatePlan/'+this.token+'/'+activation+'/'+idPlan)
    .then((response) => {
        console.log(response)
        this.getPlans();
    })
  .catch((error)=>{
    console.log(error)
  })
  },
  async updatePlan() {
      var bodyFormData = new FormData();
      bodyFormData.append('id_plan',this.idPlan)
      bodyFormData.append('nom_plan',this.nomPlan)
      bodyFormData.append('desc_plan',this.descPlan)
      bodyFormData.append('nbr_page',this.nbrPage)
      bodyFormData.append('nbr_post',this.nbrPost)
      bodyFormData.append('nbr_modification',this.nbrModification)
      bodyFormData.append('prix_mensuel_plan',this.prixMensuel)
      bodyFormData.append('prix_mise_enligne_plan',this.prixEnligne)
     await axios.post("https://admin-api.wincomedia.com/api/updatePlan/"+this.token,bodyFormData)
    .then((response)=>{
      console.log(response);
      this.getPlans();
    })
    .catch((error)=>{
      console.log(error);
    })
  },
  removePlan(idPlan) {
      axios
    .get('https://admin-api.wincomedia.com/api/removePlan/'+this.token+'/'+idPlan)
    .then((response) => {
      this.getPlans();
      console.log(response);
      
    })
    .catch((error)=>{
      console.log(error)
    })
  },
},
  mounted() {
  this.checkToken();
  this.getPlans();
  console.log(this.AllPlans);
}

  

  
};


</script>




<template>



<div class="modal fade" id="modificationModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">Plan form modification</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">


        <div class="row">
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >Plan name</label
                  >
                  
                  <argon-input type="text" v-model="this.nomPlan" />
                </div>
                <div class="col-md-8">
                  <label for="example-text-input" class="form-control-label"
                    >Plan description</label
                  >
                  <argon-input type="text" value="" v-model="this.descPlan" />
                </div>
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >How many pages ? </label
                  >
                  <argon-input type="text" value="" v-model="this.nbrPage"/>
                </div>
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >How many modifications ?</label
                  >
                  <argon-input type="text" value=""  v-model="this.nbrModification"/>
                </div>
                <div class="col-md-4">
                  <label for="example-text-input" class="form-control-label"
                    >How many posts per month ?</label
                  >
                  <argon-input  type="text" v-model="this.nbrPost" />
                </div>
                <div class="col-md-5">
                  <label for="example-text-input" class="form-control-label"
                    >Monthly price</label
                  >
                  <argon-input type="text" value="" v-model="this.prixMensuel"/>
                </div>
                <div class="col-md-5">
                  <label for="example-text-input" class="form-control-label"
                    >Deployment price</label
                  >
                  <argon-input type="text" value="" v-model="this.prixEnligne"/>
                </div>
              </div>



      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary" @click="updatePlan()" data-bs-dismiss="modal">Save changes</button>
      </div>
    </div>
  </div>
</div>


<h6>Plans table</h6>
     
      

     <div class="row">
       <div class="col my-2" v-for="plan in AllPlans" :key="plan.id">
       <div class="card" style="width: 18rem;" >
             <div class="card-body">
               <h5 class="card-title">{{ plan.nomPlan}}</h5>
               <h6 class="card-subtitle mb-2 text-muted">{{ plan.descPlan}}</h6>
               <p class="card-text">Nombre de page {{ plan.nbrPage}}</p>
               <p class="card-text">Nombre de modification {{ plan.nbrModification}}</p>
               <p class="card-text">Nombre de post {{plan.nbrPost}}</p>
               <p class="card-text">Prix mensuel {{ plan.prixMensuelPlan}} $</p>
               <p class="card-text">Prix Mise en ligne {{ plan.prixMiseEnLignePlan}} $</p>
               <span class="badge badge-sm bg-gradient-success mx-1 my-1 cursor-pointer" @click="activatePlan(plan.id,1)" v-if="plan.isActive===0">Activer</span>
               <span class="badge badge-sm bg-gradient-warning mx-1 my-1 cursor-pointer" @click="activatePlan(plan.id,0)" v-if="plan.isActive===1">Desactiver</span>
             <span class="badge badge-sm bg-gradient-info mx-1 my-1 cursor-pointer" data-bs-toggle="modal" data-bs-target="#modificationModal" @click="getSpecificPlans(plan.id)">Modifier</span>
             
             <span class="badge badge-sm bg-gradient-danger mx-1 my-1 cursor-pointer" @click="removePlan(plan.id)">Supprimer</span>
             </div>
           </div>
           </div>
         </div>
                   

                


  </template>
  
