

<template>

<div class="card">
      <div class="card-header pb-0">
        <h6>Liste des commandes</h6>
      </div>
      <div class="card-body px-0 pt-0 pb-2">


      <ul class="list-group">
        <li class="list-group-item border-0 d-flex p-4 mb-2 bg-gray-100 border-radius-lg" v-for="commande in allCommands" :key="commande.id">
          
          <div class="d-flex flex-column">
            <h6 class="mb-3 text-sm">{{ commande.nomClient }} {{ commande.prenomClient }}</h6>
            <span class="mb-2 text-xs">
              Company Name:
              <span class="text-dark font-weight-bold ms-sm-2">{{ commande.nomSite }}</span>
            </span>
            <span class="mb-2 text-xs">
              Domaine Name:
              <span class="text-dark font-weight-bold ms-sm-2">{{ commande.domaine }}</span>
            </span>
            <span class="mb-2 text-xs">
              Email Address:
              <span class="text-dark ms-sm-2 font-weight-bold">{{ commande.email }}</span>
            </span>
            <span class="text-xs">
              PHONE Number:
              <span class="text-dark ms-sm-2 font-weight-bold">{{ commande.phone }}</span>
            </span>
          </div>
          
          <div class="d-flex align-items-center">
            <argon-button
              color="danger"
              variant="outline"
              size="sm"
              class="btn-icon-only btn-rounded mb-0 me-3 d-flex align-items-center justify-content-center"
            >
              
            </argon-button>
            <div class="d-flex flex-column">
              
              <span class="text-xs mb-2"><ul><h6 class="mb-1 text-warning text-sm">{{ commande.nom_plan }}</h6><li>
                Abonnement mensuel + $ {{ commande.prix_mensuel_plan }}
              </li>
              <li> Prix mise en ligne  +  $ {{ commande.prix_miseEnLigne_plan }}</li></ul> </span>
            
            </div>
          </div>
          <h4 class="mx-5 my-3 text-success"> <span class="fs-6 text-muted">Total</span><br>{{ commande.prix_mensuel_plan+commande.prix_miseEnLigne_plan }} $</h4>
          <button class="btn mb-0 bg-success btn-md text-white h-25 mt-4 ms-2" v-if="commande.Etat===0" @click="comfirmCommand(commande.id)">Confirmer la commande</button>
          <button class="btn mb-0 bg-info btn-md text-white h-25 mt-4 ms-2" v-else disabled>Commande Confirme</button>

        </li>
        </ul>
      </div>
      </div>

</template>

<script>
import axios from 'axios'
export default {
name:"CommandsTable",
data(){
    return{
        allCommands:[],
        token:document.cookie,

    }
},
methods:{
  checkToken(){
      axios.get('https://admin-api.wincomedia.com/api/checkToken/'+this.token).then((response)=>{
        console.log(response);

      })
      .catch((error)=>{
        console.log(error);
        this.$router.replace(this.$route.query.redirect || '/signin');

      })
    },
    getCommandsList(){
        axios
        .get('https://admin-api.wincomedia.com/api/CommandesList/'+this.token)
        .then((response) => {
        this.allCommands = response.data
        
        
        })
        .catch((error)=>{
          console.log(error);
        })
    },

    comfirmCommand(idCommande){
      axios
        .get('https://admin-api.wincomedia.com/api/confirmCommande/'+this.token+'/'+idCommande)
        .then((response) => {
        console.log(response);  
        this.getCommandsList();    
        })
        .catch((error)=>{
          console.log(error);
        })
    },

},
mounted(){
    
    this.checkToken();
    this.getCommandsList();
    console.log(this.allCommands);
}    

};
</script>