<template>
  <div class="container top-0 position-sticky z-index-sticky">
    <div class="row">
      
    </div>
  </div>
  <main class="mt-0 main-content">
    <section>
      <div class="page-header min-vh-100">
        <div class="container">
          <div class="row">
            <div class="mx-auto col-xl-4 col-lg-5 col-md-7 d-flex flex-column mx-lg-0">
              <div class="card card-plain">
                <div class="pb-0 card-header text-start">
                  <h4 class="font-weight-bolder">Sign In</h4>
                  <p class="mb-0">Enter your email and password to sign in</p>
                </div>
                <div class="card-body">
                  
                    <div class="mb-3">
                      
                      <argon-input type="email" placeholder="Email" name="email" size="lg" v-model="this.username"/>
                    </div>
                    <div class="mb-3">
                      <argon-input type="password" placeholder="Password" name="password" size="lg" v-model="this.password"/>
                    </div>
                    <argon-switch id="rememberMe">Remember me</argon-switch>

                    <div class="text-center">
                      <argon-button
                      @click="this.authenticationTreatement()"
                        class="mt-4"
                        variant="gradient"
                        color="success"
                        fullWidth
                        size="lg"
                        
                      >Sign in</argon-button>
                    </div>
                  
                </div>
                <div class="px-1 pt-0 text-center card-footer px-lg-2">
                  <p class="mx-auto mb-4 text-sm">
                    Modified by 
                    <a
                      href="javascript:;"
                      class="text-success text-gradient font-weight-bold"
                    >Yassine Chiyabi</a>
                  </p>
                </div>
              </div>
            </div>
            <div
              class="top-0 my-auto text-center col-6 d-lg-flex d-none h-100 pe-0 position-absolute end-0 justify-content-center flex-column"
            >
              <div
                class="position-relative bg-gradient-primary h-100 m-3 px-7 border-radius-lg d-flex flex-column justify-content-center overflow-hidden"
                style="background-image: url('https://raw.githubusercontent.com/creativetimofficial/public-assets/master/argon-dashboard-pro/assets/img/signin-ill.jpg');
          background-size: cover;"
              >
                <span class="mask bg-gradient-success opacity-6"></span>
                <h4
                  class="mt-5 text-white font-weight-bolder position-relative"
                >"Attention is the new currency"</h4>
                <p
                  class="text-white position-relative"
                >The more effortless the writing looks, the more effort the writer actually put into the process.</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import axios from 'axios'
import ArgonInput from "@/components/ArgonInput.vue";
import ArgonSwitch from "@/components/ArgonSwitch.vue";
import ArgonButton from "@/components/ArgonButton.vue";



export default {
  name: "signin",
  components: {
    ArgonInput,
    ArgonSwitch,
    ArgonButton,
  },
  data(){
    return {
      username:"",
      password:"",
      body:document.getElementsByTagName("body")[0],
      token:document.cookie
    }
  },
  methods:{
    authenticationTreatement(){
      var bodyFormData = new FormData();
      bodyFormData.append('email',this.username);
      bodyFormData.append('password',this.password);
      axios.post("https://admin-api.wincomedia.com/api/login",bodyFormData
      ,{headers: {'Content-type': 'application/json'}}
      ).then((response)=>{
        console.log(response);

        document.cookie = response.data.token
        this.token=document.cookie
        this.beforeUnmount();
        this.$router.replace(this.$route.query.redirect || '/main');
        
       
        
        
      })
      .catch((error)=>{
        
        console.log(error);
      })

    },

    checkToken(){
      axios.get('https://admin-api.wincomedia.com/api/checkToken/'+this.token).then((response)=>{
        console.log(response);
        this.beforeUnmount();
        this.$router.replace(this.$route.query.redirect || '/main');
      })
      .catch((error)=>{
        console.log(error);

      })
    },



  created() {
    this.$store.state.hideConfigButton = true;
    this.$store.state.showNavbar = false;
    this.$store.state.showSidenav = false;
    this.$store.state.showFooter = false;
    this.body.classList.remove("bg-gray-100");
  },
  beforeUnmount() {
    this.$store.state.hideConfigButton = false;
    this.$store.state.showNavbar = true;
    this.$store.state.showSidenav = true;
    this.$store.state.showFooter = true;
    this.body.classList.add("bg-gray-100");
  },
  

},

mounted(){
  this.body = document.getElementsByTagName("body")[0];
  this.created();
  this.checkToken();
},

};
</script>
